/**
 * GeChiUI dependencies
 */
import { sprintf, _n } from '@gechiui/i18n';
import { Button } from '@gechiui/components';
import { withSelect } from '@gechiui/data';
import { backup } from '@gechiui/icons';

/**
 * Internal dependencies
 */
import PostLastRevisionCheck from './check';
import { getGCAdminURL } from '../../utils/url';
import { store as editorStore } from '../../store';

function LastRevision( { lastRevisionId, revisionsCount } ) {
	return (
		<PostLastRevisionCheck>
			<Button
				href={ getGCAdminURL( 'revision.php', {
					revision: lastRevisionId,
					gutenberg: true,
				} ) }
				className="editor-post-last-revision__title"
				icon={ backup }
			>
				{ sprintf(
					/* translators: %d: number of revisions */
					_n( '%d Revision', '%d Revisions', revisionsCount ),
					revisionsCount
				) }
			</Button>
		</PostLastRevisionCheck>
	);
}

export default withSelect( ( select ) => {
	const {
		getCurrentPostLastRevisionId,
		getCurrentPostRevisionsCount,
	} = select( editorStore );
	return {
		lastRevisionId: getCurrentPostLastRevisionId(),
		revisionsCount: getCurrentPostRevisionsCount(),
	};
} )( LastRevision );
