/**
 * External dependencies
 */
import { get } from 'lodash';

/**
 * GeChiUI dependencies
 */
import { compose } from '@gechiui/compose';
import { withSelect } from '@gechiui/data';

/**
 * Internal dependencies
 */
import { store as editorStore } from '../../store';

export function PostScheduleCheck( { hasPublishAction, children } ) {
	if ( ! hasPublishAction ) {
		return null;
	}

	return children;
}

export default compose( [
	withSelect( ( select ) => {
		const { getCurrentPost, getCurrentPostType } = select( editorStore );
		return {
			hasPublishAction: get(
				getCurrentPost(),
				[ '_links', 'gc:action-publish' ],
				false
			),
			postType: getCurrentPostType(),
		};
	} ),
] )( PostScheduleCheck );
